//引用mSearch组件，如不需要删除即可
import mSearch from '@/components/mehaotian-search-revision/mehaotian-search-revision.vue';
import {getSearchData} from '@/network/search.js'
import { getSongListData } from '@/utils/util.js'
export default {
	data() {
		return {
			defaultKeyword: "",
			keyword: "",
			oldKeywordList: [],
			hotKeywordList: [],
			keywordList: [],
			forbid: '',
			isShowKeywordList: false,
			songData:{
				pages:1,
				num:30,
				list:[
					
				]
			},
			currentKey:""
		}
	},
	onLoad() {
		this.init();
		
	},
	components: {
		//引用mSearch组件，如不需要删除即可
		mSearch
	},
	methods: {
		init() {
			this.loadDefaultKeyword();
			this.loadOldKeyword();
			this.loadHotKeyword();

		},
		blur(){
			uni.hideKeyboard()
		},
		//加载默认搜索关键字
		loadDefaultKeyword() {
			//定义默认搜索关键字，可以自己实现ajax请求数据再赋值,用户未输入时，以水印方式显示在输入框，直接不输入内容搜索会搜索默认关键字
			this.defaultKeyword = "稻香";
		},
		//加载历史搜索,自动读取本地Storage
		loadOldKeyword() {
			uni.getStorage({
				key: 'OldKeys',
				success: (res) => {
					var OldKeys = JSON.parse(res.data);
					this.oldKeywordList = OldKeys;
				}
			});
		},
		//加载热门搜索
		loadHotKeyword() {
			//定义热门搜索关键字，可以自己实现ajax请求数据再赋值
			this.hotKeywordList = ['键盘', '鼠标', '显示器', '电脑主机', '蓝牙音箱', '笔记本电脑', '鼠标垫', 'USB', 'USB3.0'];
		}, 
		//监听输入
		inputChange(event) {
			//兼容引入组件时传入参数情况
			var keyword = event.detail?event.detail.value:event;
			if (!keyword) {
				this.songData.list = [];
				this.isShowKeywordList = false;
				return;
			}
			this.isShowKeywordList = false;
			this.isShowKeywordList = true;
			//以下示例截取淘宝的关键字，请替换成你的接口
			// uni.request({
			// 	url: 'https://suggest.taobao.com/sug?code=utf-8&q=' + keyword, //仅为示例
			// 	success: (res) => {
			// 		// this.keywordList = this.drawCorrelativeKeyword(res.data.result, keyword);
			// 	}
			// });
			this.songData.list = []
			getSearchData(this.songData.pages,
							this.songData.num,
							keyword).then(data => {
								getSongListData(data,keyword,this.songData.list,this.drawCorrelativeKeyword)
							})				
		},
		// 刷新滚动条加载更多
		reflash(){
			this.songData.pages ++
			getSearchData(this.songData.pages,
							this.songData.num,
							this.keyword).then(data => {
								getSongListData(data,this.keyword,this.songData.list,this.drawCorrelativeKeyword)
							})
		},
		//高亮关键字
		drawCorrelativeKeyword(keywords, keyword) {
			var len = keywords.length,
				keywordArr = [];
			for (var i = 0; i < len; i++) {
				var row = keywords[i];
				//定义高亮#9f9f9f
				var html = row.songname.replace(keyword, "<span style='color: #9f9f9f;'>" + keyword + "</span>");
				html = '<div>' + html + '</div><div>'+ row.singer[0].name +'</div>';
				var tmpObj = {
					keyword: row.songname,
					htmlStr: html
				};
				keywordArr.push(tmpObj)
			}
			return keywordArr;
		},
		//顶置关键字
		setkeyword(data) {
			this.keyword = data.keyword;
		},
		//清除历史搜索
		oldDelete() {
			uni.showModal({
				content: '确定清除历史搜索记录？',
				success: (res) => {
					if (res.confirm) {
						console.log('用户点击确定');
						this.oldKeywordList = [];
						uni.removeStorage({
							key: 'OldKeys'
						});
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		//热门搜索开关
		hotToggle() {
			this.forbid = this.forbid ? '' : '_forbid';
		},
		//执行搜索
		doSearch(key) {
			key = key ? key : this.keyword ? this.keyword : this.defaultKeyword;
			this.keyword = key;
			this.saveKeyword(key); //保存为历史 
			uni.showToast({
				title: key,
				icon: 'none',
				duration: 2000
			});
			//以下是示例跳转淘宝搜索，可自己实现搜索逻辑






		},
		//保存关键字到历史记录
		saveKeyword(keyword) {
			uni.getStorage({
				key: 'OldKeys',
				success: (res) => {
					console.log(res.data);
					var OldKeys = JSON.parse(res.data);
					var findIndex = OldKeys.indexOf(keyword);
					if (findIndex == -1) {
						OldKeys.unshift(keyword);
					} else {
						OldKeys.splice(findIndex, 1);
						OldKeys.unshift(keyword);
					}
					//最多10个纪录
					OldKeys.length > 10 && OldKeys.pop();
					uni.setStorage({
						key: 'OldKeys',
						data: JSON.stringify(OldKeys)
					});
					this.oldKeywordList = OldKeys; //更新历史搜索
				},
				fail: (e) => {
					var OldKeys = [keyword];
					uni.setStorage({
						key: 'OldKeys',
						data: JSON.stringify(OldKeys)
					});
					this.oldKeywordList = OldKeys; //更新历史搜索
				}
			});
		}
	}
}
